import { LANG_TO_LOCALE, SELECTORS, SNIPPET_CODE_TO_HOTEL } from './constants';

export const listenForLanguageChange = () => {
  const languageDropdown = document.querySelector<HTMLSelectElement>(SELECTORS.languageDropdown);

  const languageLinks = languageDropdown?.querySelectorAll<HTMLAnchorElement>('a');
  languageLinks?.forEach((link) => {
    link.addEventListener('click', () => {
      //window.location.reload();
    });
  });
};

/**
 *
 * @returns The locale of the current language.
 */
export const getLocale = () => {
  const lang = localStorage.getItem('wglang') || 'fr';
  return LANG_TO_LOCALE[lang];
};

/**
 *
 * @returns A map of slug to hotel id.
 */
export const slugToHotelIdMap = () => {
  const map: { [key: string]: string } = {};
  Object.entries(SNIPPET_CODE_TO_HOTEL).forEach(([key, value]) => {
    map[value.slug] = key;
  });
  return map;
};

export const offerSlug = (hotelId: string, offerName: string) => {
  return `${hotelId.toLowerCase()}-${offerName.toLowerCase()}`;
};
